@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-dark: #121212;
  --secondary-dark: #222222;
  --text-primary: #FFFFFF;
  --text-secondary: #AAAAAA;
  --accent: #00E6FF;
  --accent-dark: #00B2CC;
  --success: #00CC88;
  --warning: #FFAA00;
  --danger: #FF5252;
}

body {
  background-color: var(--primary-dark);
  color: var(--text-primary);
  font-family: 'Poppins', 'Inter', sans-serif;
}

.dark-card {
  background-color: var(--secondary-dark);
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.accent-text {
  color: var(--accent);
}

.accent-border {
  border-color: var(--accent);
}

.progress-bar-container {
  background-color: #333333;
  border-radius: 10px;
  overflow: hidden;
  height: 8px;
}

.progress-bar {
  background-color: var(--accent);
  height: 100%;
  border-radius: 10px;
}

input.dark-input,
select.dark-input {
  background-color: #333333;
  color: var(--text-primary);
  border: 1px solid #444444;
  border-radius: 8px;
  padding: 10px 12px;
}

input.dark-input:focus,
select.dark-input:focus {
  border-color: var(--accent);
  outline: none;
}

button.primary-button {
  background-color: var(--accent);
  color: #000000;
  font-weight: 600;
  padding: 10px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

button.primary-button:hover {
  background-color: var(--accent-dark);
}

button.secondary-button {
  background-color: #333333;
  color: var(--text-primary);
  padding: 10px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

button.secondary-button:hover {
  background-color: #444444;
}

.circular-progress {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circular-progress svg {
  transform: rotate(-90deg);
}

.circular-progress circle {
  stroke-linecap: round;
  transition: stroke-dashoffset 0.5s ease;
}

.circular-progress-bg {
  stroke: #333333;
}

.circular-progress-fill {
  stroke: var(--accent);
}

.metric-card {
  background-color: var(--secondary-dark);
  border-radius: 12px;
  padding: 16px;
  display: flex;
  flex-direction: column;
}

.metric-title {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.metric-value {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
}

.metric-accent {
  color: var(--accent);
}

.topnav {
  background-color: var(--secondary-dark);
  padding: 12px 16px;
}

.topnav button {
  background-color: transparent;
  color: var(--text-secondary);
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.topnav button.active {
  background-color: #333333;
  color: var(--accent);
}

.topnav button:hover:not(.active) {
  background-color: #333333;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: var(--primary-dark);
}

::-webkit-scrollbar-thumb {
  background: #555555;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #777777;
} 